@import "./_header-menu.scss";
@font-face {
	font-family: "TT Lakes";
	src: url("/public/assets/fonts/TTLakes-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: "TT Lakes";
	src: url("/public/assets/fonts/TTLakes-Medium.woff") format("woff");
	font-weight: 500;
	font-style: normal;
}

@font-face {
	font-family: "TT Lakes";
	src: url("/public/assets/fonts/TTLakes-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
}

/* Mobile sceeen 320 */

.mobile--hidden {
	display: none;
}

html {
	box-sizing: border-box;
}

body {
	min-width: 320px;
	height: auto;
	font-family: "TT Lakes";
}

main {
	background-color: #f8f8f8;
}

/* Upper Panel */

.header {
	display: flex;
	white-space: wrap;
	padding: 18px 16px;
	align-items: center;
	background-color: #ffffff;
	border-bottom: 1px solid #d9fff5;
	height: 88px;

	&__burger {
		background-color: #ff3e79;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-image: url(/public/assets/icons/burger.svg);
		background-repeat: no-repeat;
		background-position: center;
		order: -6;
	}

	&__logo {
		padding-left: 16px;
		order: -4;
	}

	&__divider {
		border-radius: 1px;
		background: #eaeaea;
		width: 2px;
		height: 32px;
		margin-left: auto;
		margin-right: 20px;
		order: -3;
	}

	&__quick-menu2 {
		display: none;
		order: 0;
	}

	&__quick-menu1 {
		display: flex;
		gap: 16px;
		order: 0;
	}
}

.header__request,
.desktop-menu__request {
	background-color: #ff3e79;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-image: url(/public/assets/icons/repair.svg);
	background-repeat: no-repeat;
	background-position: center;
}

.header__status,
.desktop-menu__status {
	background-color: #ff3e79;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-image: url(/public/assets/icons/checkstatus.svg);
	background-repeat: no-repeat;
	background-position: center;
}

/* Title text */
.title>.desktop-menu {
	display: none;
}

.title {
	padding-top: 24px;

	&__text {
		font-weight: bold;
		font-size: 28px;
		line-height: 40px;
		margin-left: 16px;
	}

	&__text::before {
		content: "";
		border-right: 4px solid #00f8cf;
		border-top-right-radius: 4px;
		border-bottom-right-radius: 4px;
		position: relative;
		left: -16px;
	}
}

/* Scroll list */
.scroll-wrapper {
	position: relative;

	&::before {
		content: "";
		background: linear-gradient(90deg,
				#f8f8f8 16.84%,
				rgba(248, 248, 248, 0) 100%);
		width: 24px;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 1;
	}

	&::after {
		content: "";
		background: linear-gradient(270deg,
				#f8f8f8 16.84%,
				rgba(248, 248, 248, 0) 100%);
		width: 24px;
		height: 100%;
		position: absolute;
		right: 0;
		top: 0;
	}
}

.scroll-list {
	display: flex;
	flex-wrap: nowrap;
	overflow-x: scroll;
	scroll-behavior: smooth;
	font-size: 16px;
	line-height: 24px;
	font-weight: 500;
	letter-spacing: -0.2px;
	padding-left: 12px;
	color: #7e7e82;
	padding-top: 24px;
	padding-bottom: 2px;
	gap: 4px;

	&::-webkit-scrollbar {
		overflow: hidden;
	}

	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		white-space: nowrap;
		font-size: 16px;
		font-family: "TT Lakes";
		font-weight: 500;
		line-height: 24px;
		padding: 8px 10px 8px 16px;
		margin-left: 4px;
		transition: border-color 0.3s;
		text-decoration: none;
		color: inherit;
	}

	&__btn:hover {
		border: #b8ffec;
		border-radius: 6px;
		background-color: #ffffff;
	}

	&__btn:focus {
		outline: 2px solid #b8ffec;
		border-radius: 6px;
		background-color: #ffffff;
	}
}

/* Main text */
.about-us {
	&__description {
		padding: 0 16px;
	}

	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;

	&__text {
		font-size: 14px;
		line-height: 24px;
		font-weight: 400;
		letter-spacing: 0.2px;
		min-width: 288px;
		height: 130px;
		overflow: hidden;
	}

	&__button {
		margin: 0;
		font-size: 16px;
		font-weight: 500;
		line-height: 24px;
		letter-spacing: -0.2px;
	}

	&__button::before {
		content: url(/public/assets/icons/expand.svg);
		vertical-align: middle;
		margin-right: 8px;
		margin-left: 10px;
	}

	&__banner {
		margin: 16px auto 0;
	}
}

/* Tablet sceeen 768*/
@media screen and (min-width: 768px) {
	main>.scroll-list {
		margin-bottom: 10px;
	}

	.tabled--hidden {
		display: none;
	}

	body {
		height: auto;
		min-width: 768px;
	}

	.header {
		&__divider2 {
			border-radius: 1px;
			background: #eaeaea;
			width: 2px;
			height: 32px;
			margin-left: 20px;
			order: -4;
		}

		&__quick-menu2 {
			display: flex;
			gap: 16px;
			order: -2;
			margin-left: auto;
		}

		&__divider {
			order: -1;
			margin-left: 20px;
		}

		&_quick-menu1 {
			gap: 16px;
			order: -1;
		}

		&__call {
			background-color: #ff3e79;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			background-image: url(/public/assets/icons/call.svg);
			background-repeat: no-repeat;
			background-position: center;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&__message {
			background-color: #ff3e79;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			background-image: url(/public/assets/icons/message.svg);
			background-repeat: no-repeat;
			background-position: center;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&__profile {
			width: 40px;
			height: 40px;
			border-radius: 50%;
			background-color: #ff3e79;
			background-image: url(/public/assets/icons/profile.svg);
			background-repeat: no-repeat;
			background-position: center;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}

	.title {
		padding-left: 24px;
		padding-right: 24px;

		&__text {
			margin-left: 0;
		}

		&__text::before {
			left: -24px;
		}
	}

	.about-us {
		display: flex;
		height: auto;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items: flex-start;
		padding: 0 24px;

		&__description {
			padding: 0;
		}

		&__text {
			font-family: inherit;
			margin-bottom: 16px;
			font-size: 14px;
			line-height: 20px;
			font-weight: 400;
			letter-spacing: 0.2px;
			width: 350px;
			height: auto;
			overflow: hidden;
		}

		.mobile--hidden {
			display: flex;
		}

		&__banner {
			margin-top: 0;
			align-self: baseline;
			height: 208px;
			width: 360px;
		}

		.about-us__text+.about-us__text {
			height: 43px;
		}
	}
}

/* Desktop sceeen 1120*/
@media screen and (min-width: 1120px) {
	body {
		background-color: #f8f8f8;
	}

	main {
		padding: 0 calc((100vw - 1120px) / 2);
	}

	main>.scroll-list {
		margin-bottom: 0;
	}

	.header--hidden {
		display: none;
	}

	.title {
		display: flex;
		justify-content: space-between;
		padding-top: 42px;
	}

	.title>.desktop-menu {
		display: flex;
		gap: 162px;
		margin-right: 8px;
	}

	.desktop-menu__request--text,
	.desktop-menu__status--text {
		position: relative;
	}

	.desktop-menu__request--text::before {
		content: "Оставить заявку";
		white-space: nowrap;
		position: absolute;
		right: 48px;
		top: 9px;
		font-family: inherit;
		font-weight: 500;
	}

	.desktop-menu__status--text::before {
		content: "Статус ремонта";
		white-space: nowrap;
		position: absolute;
		right: 48px;
		top: 9px;
		font-family: inherit;
		font-weight: 500;
	}

	.scroll-list {
		display: flex;
		margin-bottom: 0;
		flex-wrap: wrap;
		padding: 8px 32px;
	}

	.about-us {
		&__text {
			padding-top: 16px;
			padding-left: 8px;
			width: 490px;
		}

		&__banner {
			min-width: 531px;
			height: 307px;
			padding-top: 7px;
		}

		.about-us__text+.about-us__text {
			height: auto;
		}
	}

	.tabled--hidden {
		display: flex;
		text-align: center;
	}
}